"use client";

import type { CsvProcessingEntry } from "@/types/csv-status";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from "@/components/ui/dialog";
import { Badge } from "@/components/ui/badge";
import { getStatusClassNames } from "./StatusBadge";
import { formatDuration } from "@/lib/utils";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { ScrollArea } from "@/components/ui/scroll-area";
import knownHeaders from "@/known_headers.json"; 
import React, { useState } from "react";

interface FileDetailDialogProps {
  entry: CsvProcessingEntry | null;
  isOpen: boolean;
  onOpenChange: (isOpen: boolean) => void;
}

const DetailRow = ({ label, value }: { label: string, value: React.ReactNode }) => (
  <div className="grid grid-cols-2 gap-4 py-2 first:pt-0 last:pb-0">
    <dt className="text-sm font-medium text-muted-foreground">{label}</dt>
    <dd className="text-sm text-right font-mono">{value}</dd>
  </div>
);

const CRITICAL_HEADERS = [
  "digid_email",
  "pwd_plain",
  "pdata_id_ssn_number",
  "pdata_id_nid_number"
];

function formatFileSize(bytes?: number): string {
  if (bytes == null || isNaN(bytes)) return 'N/A';
  if (bytes >= 1024 * 1024) return (bytes / (1024 * 1024)).toFixed(2) + ' MB';
  if (bytes >= 1024) return (bytes / 1024).toFixed(2) + ' KB';
  return bytes + ' bytes';
}

// Utility to format line numbers as ranges
function formatLineRanges(lines?: number[]): string {
  if (!lines || lines.length === 0) return '-';
  const sorted = [...lines].sort((a, b) => a - b);
  const ranges: string[] = [];
  let start = sorted[0];
  let end = sorted[0];
  for (let i = 1; i < sorted.length; i++) {
    if (sorted[i] === end + 1) {
      end = sorted[i];
    } else {
      ranges.push(start === end ? `${start}` : `[${start}-${end}]`);
      start = end = sorted[i];
    }
  }
  ranges.push(start === end ? `${start}` : `[${start}-${end}]`);
  return ranges.join(', ');
}

function formatUTCDate(dateString?: string): string {
  if (!dateString) return 'N/A';
  try {
    const d = new Date(dateString);
    if (isNaN(d.getTime())) return 'N/A';
    return d.toLocaleString('en-US', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit',
      hour12: true,
      timeZone: 'UTC'
    }) + ' UTC';
  } catch (e) {
    return 'Invalid Date';
  }
}

export function FileDetailDialog({ entry, isOpen, onOpenChange }: FileDetailDialogProps) {
  if (!entry) return null;

  const totalDuration = entry.duration_ms
    ? formatDuration(entry.duration_ms)
    : (entry.start_time && entry.status === 'running'
        ? formatDuration(new Date().getTime() - new Date(entry.start_time).getTime()) + ' (running)'
        : "N/A");

  const invalidLines = entry.invalid_line_numbers?.length ? entry.invalid_line_numbers.length.toLocaleString() : '-';


  return (
    <Dialog open={isOpen} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-4xl h-[80vh]">
        <DialogHeader>
          <DialogTitle className="truncate text-lg">{entry.filename}</DialogTitle>
          <DialogDescription>
            Detailed processing information and status for this file.
          </DialogDescription>
        </DialogHeader>
        <ScrollArea className="h-full pr-6 -mr-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
            {/* Left Column */}
            <div className="space-y-6">
              <h3 className="text-base font-semibold text-foreground">File Metadata</h3>
              
              <div className="space-y-1">
                <h4 className="text-sm font-medium text-muted-foreground">Timestamps</h4>
                <dl className="divide-y divide-border/50 border-t border-border/50">
                  <DetailRow label="Insertion Date" value={formatUTCDate(entry.insertion_date)} />
                  <DetailRow label="Processing Start" value={formatUTCDate(entry.start_time)} />
                  <DetailRow label="Processing End" value={formatUTCDate(entry.end_time)} />
                  <DetailRow label="Total Duration" value={totalDuration} />
                </dl>
              </div>

              <div className="space-y-1">
                <h4 className="text-sm font-medium text-muted-foreground">File Metrics</h4>
                 <dl className="divide-y divide-border/50 border-t border-border/50">
                    <DetailRow label="Original File Size" value={formatFileSize(entry.original_file_size)} />
                    <DetailRow label="Original Row Count" value={entry.original_row_count?.toLocaleString() ?? 'N/A'} />
                    <DetailRow label="Final File Size" value={formatFileSize(entry.final_file_size)} />
                    <DetailRow label="Final Row Count" value={entry.final_row_count?.toLocaleString() ?? 'N/A'} />
                    <DetailRow label="Valid Row Percentage" value={entry.valid_row_percentage != null ? entry.valid_row_percentage.toFixed(2) + '%' : 'N/A'} />
                    <DetailRow label="Invalid Lines" value={invalidLines} />
                 </dl>
              </div>

              <div className="space-y-1">
                <h4 className="text-sm font-medium text-muted-foreground">AI Usage</h4>
                 <dl className="divide-y divide-border/50 border-t border-border/50">
                    <DetailRow label="AI Model Used" value={entry.ai_model ?? 'N/A'} />
                    <DetailRow label="Gemini Input Tokens" value={entry.gemini_input_tokens?.toLocaleString() ?? 'N/A'} />
                    <DetailRow label="Gemini Output Tokens" value={entry.gemini_output_tokens?.toLocaleString() ?? 'N/A'} />
                    <DetailRow label="Gemini Total Tokens" value={entry.gemini_total_tokens?.toLocaleString() ?? 'N/A'} />
                    <DetailRow label="Estimated Cost" value={typeof entry.estimated_cost === 'number' ? `$${entry.estimated_cost.toFixed(6)}` : 'N/A'} />
                 </dl>
              </div>

            </div>

            {/* Right Column */}
            <div className="space-y-6">
              <div>
                <h3 className="text-base font-semibold text-foreground">Overall Status</h3>
                <div className="flex justify-between items-center py-2 border-b border-t border-border/50 mt-1">
                  <span className="text-sm text-muted-foreground">Current Status</span>
                  <Badge variant="outline" className={getStatusClassNames(entry.status)}>
                      {entry.status}
                  </Badge>
                </div>
              </div>

              <div>
                  <h3 className="text-base font-semibold text-foreground mb-1">Processing Steps</h3>
                  <Table>
                      <TableHeader>
                      <TableRow>
                          <TableHead>Step</TableHead>
                          <TableHead>Status</TableHead>
                          <TableHead className="text-right">Duration</TableHead>
                      </TableRow>
                      </TableHeader>
                      <TableBody>
                        {['classification', 'sampling', 'gemini_query', 'normalization'].map((stage) => {
                          const stat = entry.stage_stats?.[stage];
                          let duration = 'N/A';
                          if (stat?.start_time && stat?.end_time) {
                            const ms = new Date(stat.end_time).getTime() - new Date(stat.start_time).getTime();
                            duration = formatDuration(ms);
                          }
                          return (
                            <TableRow key={stage}>
                              <TableCell className="font-medium">{stage.charAt(0).toUpperCase() + stage.slice(1).replace('_', ' ')}</TableCell>
                              <TableCell>
                                <Badge variant="outline" className={getStatusClassNames(stat?.status)}>
                                  {stat?.status || 'enqueued'}
                                </Badge>
                              </TableCell>
                              <TableCell className="text-right font-mono">{duration}</TableCell>
                            </TableRow>
                          );
                        })}
                      </TableBody>
                  </Table>
              </div>

              <div>
                  <h3 className="text-base font-semibold text-foreground mb-2">Extracted Fields</h3>
                  {entry.extracted_fields && entry.extracted_fields.length > 0 ? (
                  <div className="flex flex-wrap gap-2">
                      {entry.extracted_fields.map((field, idx) => {
                        let color: string | undefined = undefined;
                        let fontWeight: string | undefined = undefined;
                        if (CRITICAL_HEADERS.includes(field)) fontWeight = 'bold';
                        else if (!(field in knownHeaders)) color = 'orange';
                        return (
                          <Badge key={`${field}-${idx}`} variant="secondary" style={{ color, fontWeight }}>{field}</Badge>
                        );
                      })}
                  </div>
                  ) : (
                  <p className="text-sm text-muted-foreground">No fields extracted yet.</p>
                  )}
              </div>
            </div>
          </div>
        </ScrollArea>
      </DialogContent>
    </Dialog>
  );
}
